/* Estilos generales del formulario */
.form-container {
    max-width: 800px;
    margin: auto;
    padding: 20px;
    background-color: rgba(255, 255, 255, 0.9);
    border: 1px solid #ccc;
    border-radius: 10px;
}

/* Estilos de las filas del formulario */
.form-row {
    display: flex;
    flex-wrap: wrap;
    /* Permitir que las columnas se envuelvan en pantallas pequeñas */
    justify-content: space-between;
}

/* Estilos de las columnas del formulario */
.form-column {
    flex-basis: calc(33.33% - 10px);
    /* Ancho aproximado del tercio de la pantalla, menos el espacio entre columnas */
    margin-bottom: 20px;
}

/* Ajuste del espacio entre columnas */
.form-column:nth-child(3n) {
    margin-right: 0;
    /* Eliminar el margen derecho de la tercera columna en cada fila */
}

/* Estilos de los campos de entrada */
.form-input {
    margin-bottom: 20px;
}

/* Estilos de las etiquetas */
.form-label {
    font-weight: bold;
}

/* Estilos del botón */
.form-button {
    display: block;
    width: 100%;
    padding: 10px;
    background-color: #ff7f50;
    /* Naranja */
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.form-button:hover {
    background-color: #e65c00;
    /* Naranja más oscuro al pasar el ratón */
}

.form-button-alt {
    background-color: #ff5050;
    /* Rojo */
}

.form-button-alt:hover {
    background-color: #cc0000;
    /* Rojo más oscuro al pasar el ratón */
}

/* Media query para pantallas pequeñas */
@media screen and (max-width: 768px) {
    .form-column {
        flex-basis: 100%;
        /* Las columnas ocupan todo el ancho en pantallas pequeñas */
        margin-right: 0;
        /* Eliminar el margen derecho para que las columnas no se superpongan */
    }
}